import React from 'react';
import { SpacingUtil } from '@ali/4ever-utils';

const ParagraphWithStyle = React.forwardRef((props: any, ref) => {
  const { node, children, style, ...rest } = props;
  return (
    <div ref={ref} {...rest} style={{ ...SpacingUtil.toStyle(node), ...style }}>
      {children}
    </div>
  );
});

export default ParagraphWithStyle;
